.nav_head{
    background: bisque;
    padding: 0;
    margin: inherit;
    height: 7vh;
    box-shadow: lightblue;
  
}

.nav_head nav a{
  
    float: left;
    color: palevioletred;
    font-size: 25PX;
    
}
.nav_head nav a:hover{
    color: red;
    background: whitesmoke;
    border-radius: 15px;
}
